body {
  color: #333;
  font-size: 20px;
  line-height: 1.5;
  font-family: "SF Pro Display";
}

::selection {
  background-color: rgba(216, 17, 70, 0.7);
  color: #fff;
}

a,
a:visited,
a:active {
  color: #000;
  text-decoration: none;
  transition: 0.15s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin: 0;
  margin-top: 15px;
}

/*==============================================================
*												GRID
=============================================================== */
.section {
  padding-top: 120px;
  padding-bottom: 120px;
}

/*==============================================================
*												BUTTONS
=============================================================== */
.btn-row {
  display: flex;
  flex-wrap: wrap;
}

.btn,
a.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-shrink: 0;
  padding: 15px 20px;
  transition: 0.15s;
  font-size: 20px;
  font-weight: bold;
}
.btn.btn-primary,
a.btn.btn-primary {
  background-color: #d81146;
  color: #fff;
}
.btn.btn-primary:hover,
a.btn.btn-primary:hover {
  background-color: white;
}
.btn.btn-border,
a.btn.btn-border {
  border: 2px solid rgba(216, 17, 70, 0.3);
  color: #d81146;
}
.btn.btn-border:hover,
a.btn.btn-border:hover {
  background-color: #d81146;
  color: #fff;
  border-color: #d81146;
}
.btn.btn-border-fff,
a.btn.btn-border-fff {
  border: 2px solid #fff;
  color: #fff;
}

.btn:disabled,
a.btn:disabled {
  pointer-events: none !important;
  cursor: auto !important;
}

