/*==============================================================
*											    MAIN SCREEN
=============================================================== */
.section-main-screen {
  min-height: 100vh;
  display: flex;
}
.section-main-screen .container {
  margin-top: auto;
  margin-bottom: auto;
}
.section-main-screen .page-title,
.section-main-screen .page-subtitle {
  margin: 0;
}
.section-main-screen .page-subtitle {
  margin-top: 60px;
}
.section-main-screen .points-row {
  display: flex;
  align-items: stretch;
  margin: -15px;
  padding-top: 60px;
}
.section-main-screen .point-col {
  padding: 15px;
  width: 100%;
}
.section-main-screen .hidden-el {
  position: relative;
  padding: 30px 20px;
  background-color: #fff;
  border-bottom: 4px solid #d81146;
  width: 100%;
  overflow: hidden;
}
.section-main-screen .hidden-el:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.2s;
  transform: translateY(100%);
  background-color: #d81146;
}
.section-main-screen .hidden-el .title,
.section-main-screen .hidden-el .hidden-desc {
  position: relative;
  z-index: 1;
  margin: 0;
  transition: 0.2s;
}
.section-main-screen .hidden-el .title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-main-screen .hidden-el .hidden-desc {
  pointer-events: none;
  user-select: none;
  opacity: 0;
  color: #fff;
}
.section-main-screen .hidden-el:hover:before {
  transform: none;
}
.section-main-screen .hidden-el:hover .title {
  user-select: none;
  pointer-events: none;
  opacity: 0;
}
.section-main-screen .hidden-el:hover .hidden-desc {
  user-select: text;
  pointer-events: all;
  opacity: 1;
}